@import "../../global.scss";

.contactFooter {
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  height: 60px;
  position: fixed;
  top: calc(100vh - 60px);
  
  @include mobile {
    height: 40px;
  }

  ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    position: fixed;
  }

  li {
    margin-right: 10px;
    margin-left: 10px;

    a {
      text-decoration: none;
      color: whitesmoke;
    }

    .icon {
      font-size: 37px;

      @include mobile {
        font-size: 30px;
      }
    }
  }
}
